        title=""
        :column="3"
        :size="size"
        border
      >
        <template #extra>
          <el-button
            type="primary"
            @click="
              teacherModData = JSON.parse(JSON.stringify(teacherData));
              teacherModFromVisible(2);
            "
            ></el-button
          >
        </template>

        <el-descriptions-item>
          <template #label>
