        </el-descriptions-item>

        <el-descriptions-item>
          <template #label>
            <div class="cell-item">
              <el-icon :style="iconStyle">
                <tickets />
              </el-icon>
              6
            </div>
          </template>
          {{ teacherData.topic6 }}
        </el-descriptions-item>

        <el-descriptions-item>
          <template #label>
            <div class="cell-item">
              <el-icon :style="iconStyle">
                <tickets />
              </el-icon>
              7
            </div>
          </template>
          {{ teacherData.topic7 }}
        </el-descriptions-item>

        <el-descriptions-item>
          <template #label>
            <div class="cell-item">
              <el-icon :style="iconStyle">
                <tickets />
              </el-icon>
              8
            </div>
          </template>
          {{ teacherData.topic8 }}
        </el-descriptions-item>
      </el-descriptions>
    </div>

    <div>
      <el-dialog v-model="teacherFromVisible" title="" width="30%">
        <el-form :model="teacherModData" label-width="120px">
          <el-form-item label="">
            <el-input v-model="teacherModData.id" :disabled="true" />
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="teacherModData.name" :disabled="true" />
          </el-form-item>

          <el-form-item label="">
            <el-select v-model="teacherModData.gender" placeholder="">
              <el-option label="" value="" />
              <el-option label="" value="" />
            </el-select>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model.number="teacherModData.age" />
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="teacherModData.department" />
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="teacherModData.major" />
          </el-form-item>

