          </el-form-item>

          <el-form-item label="">
            <el-input v-model="teacherModData.email" />
          </el-form-item>

          <el-form-item label="QQ">
            <el-input v-model="teacherModData.qq" />
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="queryModTeacher()"
              ></el-button
            >
            <el-button @click="teacherFromVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>

    <div>
      <el-dialog v-model="teacherPaperFromVisible" title="" width="30%">
        <el-form :model="teacherModData" label-width="120px">
          <el-form-item label="">
            <el-input v-model="teacherModData.researchDirection" />
          </el-form-item>

          <el-form-item label="">
            <el-select
              v-model="teacherModData.isFirstTutor"
              placeholder=""
              :disabled="true"
            >
              <el-option label="1-" value="1" />
              <el-option label="0-" value="0" />
            </el-select>
            <el-divider direction="vertical" border-style="dashed" />
            <el-tag>1-0-</el-tag>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="teacherModData.studentNum" :disabled="true" />
          </el-form-item>

          <el-form-item label="1">
            <el-input v-model.number="teacherModData.topic" />
          </el-form-item>

          <el-form-item label="2">
            <el-input v-model="teacherModData.topic2" />
          </el-form-item>

          <el-form-item label="3">
            <el-input v-model="teacherModData.topic3" />
          </el-form-item>

          <el-form-item label="4">
            <el-input v-model="teacherModData.topic4" />
          </el-form-item>

          <el-form-item label="5">
