          <el-form-item>
            <el-button type="primary" @click="studentModData()"></el-button>
            <el-button @click="studentModFormVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>

    <div>
      <el-dialog
        v-model="studentModPaperVisible"
        title=""
        width="30%"
      >
        <el-form :model="modStudentData" label-width="120px">
          <el-form-item label="">
            <el-input v-model="modStudentData.studentId" :disabled="true" />
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="modStudentData.name" :disabled="true" />
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="modStudentData.thesisType" :disabled="true" />
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="modStudentData.title" />
          </el-form-item>

          <el-form-item label="">
            <el-select v-model="modStudentData.language" placeholder="">
              <el-option label="" value="" />
              <el-option label="English" value="English" />
            </el-select>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model.number="modStudentData.researchDirection" />
          </el-form-item>

          <el-form-item label="">
            <el-select
              v-model="modStudentData.sourceTopicSelection"
              placeholder=""
            >
              <el-option label="" value="" />
              <el-option label="" value="" />
              <el-option label="" value="" />
              <el-option label="" value="" />
            </el-select>
          </el-form-item>

          <el-form-item label="">
            <el-select
              v-model="modStudentData.natureTopicSelection"
              placeholder=""
              :disabled="true"
            >
              <el-option label="1-" value="1" />
              <el-option label="2-" value="2" />
              <el-option label="3-" value="3" />
              <el-option label="4-" value="4" />
            </el-select>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="modStudentData.thesisKeyword" type="textarea" />
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="studentModData()"></el-button>
            <el-button @click="studentModPaperVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>

    <div>
      <el-dialog
        v-model="dingDingImgVisible"
        title=""
        width="30%"
      >
        <el-image
          style="width: 100%; height: 100%"
          :src="dingImgUrl"
          :fit="fit"
        />
      </el-dialog>
    </div>

    <div>
      <el-dialog v-model="changePassWordVisible" title="" width="30%">
        <el-form :model="changePassWordForm" label-width="120px">
          <el-form-item label="">
            <el-input v-model="changePassWordForm.id" :disabled="true" />
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="changePassWordForm.oldPassword" />
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="changePassWordForm.newPassword" />
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="ChangePassWord()"></el-button>
            <el-button @click="changePassWordVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>



<script>
import router from "@/router";
import axios from "axios";
import { reactive, toRefs, onMounted, getCurrentInstance } from "vue";
import { ElMessage } from "element-plus";
import { Iphone, Location, Tickets, User } from "@element-plus/icons-vue";

export default {
  components: {
    Iphone,
    Location,
    Tickets,
    User,
  },

  setup() {
    const state = reactive({
      currentStudent: {},
      size: "large",
      //   marginMap: {large: '32px',default: '28px',small: '24px',},
      downloadUrl:
        getCurrentInstance()?.appContext.config.globalProperties.$downloadUrl,
      dingImgUrl: "",

      myTeacherIdA: "",
      myTeacherDataA: {},
      myTeacherDataB: {},
      modStudentData: {},
      studentModFormVisible: false,
      studentModPaperVisible: false,
      dingDingImgVisible: false,
      changePassWordVisible: false,
      changePassWordForm: {
        id: "",
        newPassword: "",
        oldPassword: "",
        type: "2",
      },

      queryStudentData() {
        let stId = localStorage.getItem("id");
        axios({
          url:
            "/student/search?name=" +
            "&studentId=" +
            stId +
            "&stuClass=" +
            "&teacherName=" +
            "&title=" +
            "&startIndex=1&pageSize=25",
        })
          .then(function (resp) {
            state.currentStudent = resp.data["student"][0];
            let stDa = resp.data["student"][0];
            // console.log(stDa)
            state.modStudentData = JSON.parse(JSON.stringify(stDa));
            state.changePassWordForm.id = stDa["studentId"];
            state.queryTeacherData(resp.data["student"][0]["mentorId"], 1);
            state.queryTeacherData(resp.data["student"][0]["mentor2Id"], 2);
          })
          .catch((error) => {
            ElMessage({
              message: "!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
      ChangePassWord() {
        axios({
          method: "post",
          url: "/edit/pwd",
          data: state.changePassWordForm,
        })
          .then(function (resp) {
            if (resp.data == "") {
              ElMessage({
                message: "!",
                showClose: true,
                grouping: true,
                duration: 1000,
                type: "success",
              });
              localStorage.removeItem("id");
              localStorage.removeItem("token");
              localStorage.removeItem("type");
              localStorage.removeItem("name");
              router.push("/");
            } else {
              ElMessage({
                message: resp.data,
                showClose: true,
                grouping: true,
                duration: 2000,
                type: "warning",
              });
            }
          })
          .catch((error) => {
            ElMessage({
              message: "!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "warning",
            });
          });
      },
      queryTeacherData(tId, flag) {
        axios({
          url: "/teacher/get/one?teacherId=" + tId,
        }).then(function (resp) {
          if (flag == 1) {
            state.myTeacherDataA = resp.data;
          } else if (flag == 2) {
            state.myTeacherDataB = resp.data;
          }
        });
      },

      studentModData() {
        let check = {
          stName: /^[\u4e00-\u9fa5]{2,4}$/,
          age: /^(?:[1-9][0-9]?|1[01][0-9]|120)$/,
          phone:
            /^1(3[0-9]|4[01456879]|5[0-35-9]|6[2567]|7[0-8]|8[0-9]|9[0-35-9])\d{8}$/,
          email: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
          qq: /^[1-9][0-9]\d{4,11}$/,
        };
        let flag = false;
        let tips = "";
        // let stName = new RegExp('/^[\u4e00-\u9fa5]{2,4}$/')

        // state.modStudentData.name = state.modStudentData.name.trim
        // state.modStudentData.age = state.modStudentData.age.trim
        // state.modStudentData.phone = state.modStudentData.phone.trim
        // state.modStudentData.qq = state.modStudentData.qq.trim

        if (!check["stName"].test(state.modStudentData.name)) {
          flag = true;
          tips = tips + "2-4, ";
        }
        if (!check["age"].test(state.modStudentData.age)) {
          flag = true;
          tips = tips + ", ";
        }
        if (!check["phone"].test(state.modStudentData.phone)) {
          flag = true;
          tips = tips + ", ";
        }
        if (!check["email"].test(state.modStudentData.email)) {
          flag = true;
          tips = tips + ", ";
        }
        if (!check["qq"].test(state.modStudentData.qq)) {
          flag = true;
          tips = tips + "QQ, ";
        }

        if (flag) {
          ElMessage({
            message: tips,
            showClose: true,
            grouping: true,
            duration: 2000,
            type: "warning",
          });
        } else {
          state.queryModData();
        }

        // let stId = state.currentStudent.studentId;
        // console.log(stId);
      },

      queryModData() {
        state.studentModPaperVisible = false;
        state.studentModFormVisible = false;
        axios({
          method: "put",
          url: "/student",
          data: state.modStudentData,
        })
          .then(function (resp) {
            state.queryStudentData();
            if (resp.data == true) {
              ElMessage({
                message: "!",
                showClose: true,
                grouping: true,
                duration: 1000,
                type: "success",
              });
            } else {
              ElMessage({
                message: "!",
                showClose: true,
                grouping: true,
                duration: 2000,
                type: "warning",
              });
            }
          })
          .catch((error) => {
            ElMessage({
              message: "! " + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
      dingding(flag) {
        if (flag == 1) {
          let url = state.myTeacherDataA.image;
          let formatUrl = url.replace(/\\/g, "/");
          state.dingImgUrl = state.downloadUrl + "/" + formatUrl;
        } else if (flag == 2) {
          let url = state.myTeacherDataB.image;
          let formatUrl = url.replace(/\\/g, "/");
          state.dingImgUrl = state.downloadUrl + "/" + formatUrl;
        }
        // console.log(state.dingImgUrl)
        state.dingDingImgVisible = true;
      },
    });

    onMounted(() => {
      state.queryStudentData();
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>



