<template>
  <div id="pageView">
    <div>
      <el-form :inline="true" :model="searchData" class="demo-form-inline">
        <!-- <el-form-item label="">
          <el-input v-model="searchData.studentId" placeholder="" />
        </el-form-item> -->

        <!-- <el-form-item label="">
          <el-input v-model="searchData.name" placeholder="" />
        </el-form-item> -->

        <!-- <el-form-item label="">
          <el-input v-model="searchData.title" placeholder="" />
        </el-form-item> -->

        <!-- <el-form-item>
          <el-button type="primary" @click="queryStudentList()"
            ></el-button
          >
        </el-form-item> -->

        <!-- <el-form-item>
          <el-button type="primary" @click="emptyClick()"></el-button>
        </el-form-item> -->

        <el-form-item>
          <el-button type="warning">
            {{ followStudentSize }}{{ teacherQuota }}{{
              followStudentSize - teacherQuota
            }}</el-button
          >
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="resetList()"></el-button>
        </el-form-item>

             <el-form-item>
          <el-button type="primary" @click="ExportFormButton()"></el-button>
        </el-form-item>

        <!-- <el-form-item>
          <el-button type="primary" @click="multipleSelectButton()"
            ></el-button
          >
        </el-form-item> -->
      </el-form>
    </div>

    <div>
      <el-table
        :data="studentList"
        stripe
        style="width: 100%"
        @selection-change="handleSelectionChangeButton"
      >
        <el-table-column type="selection" width="55"></el-table-column>

        <el-table-column type="index" align="center" width="55" label="" />

        <el-table-column align="center" prop="name" label="" />

        <el-table-column align="center" prop="studentId" label="" />

        <el-table-column align="center" prop="specialities" label="" />

        <el-table-column align="center" prop="stuClass" label="" />

        <el-table-column align="center" prop="title" label="" />

        <el-table-column align="center" prop="phone" label="" />

        <!-- <el-table-column align="center" prop="email" label="" /> -->

        <el-table-column align="center" label="">
          <template #default="scope">
            <el-button type="primary" @click="teacherModStudent(scope.row, 1)"
              >/</el-button
            >
            <el-button type="primary" @click="teacherModStudent(scope.row, 2)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->

    <!-- <div>
      <el-pagination
        v-model:currentPage="page.currentPage"
        v-model:page-size="page.pageSize"
        :page-sizes="[20, 50, 80, 120]"
        :small="small"
        :disabled="disabled"
        :background="background"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.studentCount"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div> -->

    <div>
      <el-dialog v-model="studentFromVisible" title="" width="30%">
        <el-tag class="ml-2" type="success"
          ></el-tag
        >

        <el-form :model="tempStudentFrom" label-width="120px">
          <el-form-item label="">
            <el-input v-model="tempStudentFrom.studentId" :disabled="true" />
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="tempStudentFrom.name" :disabled="true" />
          </el-form-item>

          <el-form-item label="1">
            <el-input v-model="tempStudentFrom.mentorId" :disabled="true" />
          </el-form-item>

          <el-form-item label="2">
            <el-input v-model="tempStudentFrom.mentor2Id" />
            <el-tag class="ml-2" type="success"
              ></el-tag
            >
            <el-tag class="ml-2" type="success"
              ></el-tag
            >
          </el-form-item>

          <el-form-item label="">
            <el-select
              v-model="tempStudentFrom.isCompletePractice"
              placeholder=""
            >
              <el-option label="1-" value="1" />
              <el-option label="0-" value="0" />
            </el-select>
            <el-tag class="ml-2" type="success">1-0-</el-tag>
          </el-form-item>

          <el-form-item label="">
            <el-select
              v-model="tempStudentFrom.isFirstSession"
              placeholder=""
            >
              <el-option label="1-" value="1" />
              <el-option label="0-" value="0" />
            </el-select>
            <el-tag class="ml-2" type="success">1-0-</el-tag>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="tempStudentFrom.thesisType" :disabled="true"/>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="tempStudentFrom.title" />
          </el-form-item>

          <el-form-item label="">
            <el-select v-model="tempStudentFrom.language" placeholder="">
              <el-option label="" value="" />
              <el-option label="English" value="English" />
            </el-select>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model.number="tempStudentFrom.researchDirection" />
          </el-form-item>

          <el-form-item label="">
            <el-select
              v-model="tempStudentFrom.sourceTopicSelection"
              placeholder=""
            >
              <el-option label="" value="" />
              <el-option label="" value="" />
              <el-option label="" value="" />
              <el-option label="" value="" />
            </el-select>
          </el-form-item>

          <el-form-item label="">
            <el-select
              v-model="tempStudentFrom.natureTopicSelection"
              placeholder=""
              :disabled="true"
            >
              <el-option label="1-" value="1" />
              <el-option label="2-" value="2" />
              <el-option label="3-" value="3" />
              <el-option label="4-" value="4" />
            </el-select>
            <br />
            <el-tag class="ml-2" type="success">
              1-2-3-4-</el-tag
            >
          </el-form-item>

          <el-form-item label="">
            <el-input v-model.number="tempStudentFrom.thesisKeyword" />
          </el-form-item>

          <el-form-item label="">
            <el-input
              v-model.number="tempStudentFrom.repetitionRate"
              :disabled="isEdit"
            />
          </el-form-item>

          <el-form-item label="">
            <el-input
              v-model.number="tempStudentFrom.mentorScore"
              :disabled="isEdit"
            />
          </el-form-item>

          <el-form-item label="">
            <el-input
              v-model.number="tempStudentFrom.appraiserScore"
              :disabled="isEdit"
            />
          </el-form-item>

          <el-form-item label="">
            <el-input
              v-model.number="tempStudentFrom.defenseScore"
              :disabled="isEdit"
            />
          </el-form-item>

          <el-form-item label="">
            <el-input
              v-model.number="tempStudentFrom.comprehensiveScore"
              :disabled="isEdit"
            />
          </el-form-item>

          <el-form-item label="">
            <el-select
              v-model="tempStudentFrom.evaluationGrade"
              placeholder=""
              :disabled="isEdit"
            >
              <el-option label="1-" value="1" />
              <el-option label="2-" value="2" />
              <el-option label="3-" value="3" />
              <el-option label="4-" value="4" />
            </el-select>
            <br />
            <el-tag class="ml-2" type="success"
              >1-2-3-4-</el-tag
            >
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="queryModStudentData()"
              ></el-button
            >
            <el-button @click="studentFromVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>

    <div>
      <el-dialog v-model="studenFileVisible" title="" width="30%">
        <el-tag class="ml-2" type="success" v-if="isEdit == false"
          ></el-tag
        >

        <el-form :model="tempStudentFrom" label-width="120px">
          <el-form-item label="">
            <el-input v-model="tempStudentFrom.studentId" :disabled="true" />
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="tempStudentFrom.name" :disabled="true" />
          </el-form-item>

          <el-form-item label="1">
            <el-divider direction="vertical" border-style="dashed" />
            <el-tag
              class="ml-2"
              type="warning"
              v-if="
                tempStudentFrom.file == null || tempStudentFrom.file.length == 0
              "
            >
              
            </el-tag>

            <span v-else>
              <el-tag class="ml-2" type="success">{{
                tempStudentFrom.file.split("\\")[
                  tempStudentFrom.file.split("\\").length - 1
                ]
              }}</el-tag>
              <el-divider direction="vertical" border-style="dashed" />
              <el-button
                type="success"
                @click="DownStudentFile(1)"
                v-if="tempStudentFrom.file != null"
                >/</el-button
              >
            </span>
          </el-form-item>

          <el-form-item label="1">
            <el-input v-model="tempStudentFrom.notes3" type="textarea" />
          </el-form-item>

          <el-form-item label="2">
            <el-divider direction="vertical" border-style="dashed" />
            <el-tag
              class="ml-2"
              type="warning"
              v-if="
                tempStudentFrom.file2 == null ||
                tempStudentFrom.file2.length == 0
              "
            >
              
            </el-tag>
            <span v-else>
              <el-tag class="ml-2" type="success">{{
                tempStudentFrom.file2.split("\\")[
                  tempStudentFrom.file2.split("\\").length - 1
                ]
              }}</el-tag>
              <el-divider direction="vertical" border-style="dashed" />
              <el-button type="success" @click="DownStudentFile(2)"
                >/</el-button
              >
            </span>
          </el-form-item>

          <el-form-item label="2">
            <el-input v-model="tempStudentFrom.notes4" type="textarea" />
          </el-form-item>

          <el-form-item label="3">
            <el-divider direction="vertical" border-style="dashed" />
            <el-tag
              class="ml-2"
              type="warning"
              v-if="
                tempStudentFrom.file3 == null ||
                tempStudentFrom.file3.length == 0
              "
            >
              
            </el-tag>
            <span v-else>
              <el-tag class="ml-2" type="success">{{
                tempStudentFrom.file3.split("\\")[
                  tempStudentFrom.file3.split("\\").length - 1
                ]
              }}</el-tag>
              <el-divider direction="vertical" border-style="dashed" />
              <el-button type="success" @click="DownStudentFile(3)"
                >/</el-button
              >
            </span>
          </el-form-item>

          <el-form-item label="3">
            <el-input v-model="tempStudentFrom.notes5" type="textarea" />
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="queryModStudentData()"
              ></el-button
            >
            <el-button @click="studenFileVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>



  </div>
</template>


<script>
import axios from "axios";
import { reactive, toRefs, onMounted, getCurrentInstance } from "vue";
import { ElMessage } from "element-plus";

export default {
  name: "TeacherHomeStudentList",
  setup() {
    const state = reactive({
      uploadUrl:
        getCurrentInstance()?.appContext.config.globalProperties.$uploadUrl,
      downloadUrl:
        getCurrentInstance()?.appContext.config.globalProperties.$downloadUrl,
      slogan: "",
      searchData: { studentId: "", name: "", title: "", tutorFalg: "" },
      dialogVisible: false,
      studentList: [],
      // studentTotal: 20,
      multipleSelectionData: [],
      page: { currentPage: 1, pageSize: 25, studentCount: 0 },
      formatSelect: {
        allSelect: 0,
        notSelect: 0,
        okSelect: 0,
        notSelectName: "",
        okSelectName: "",
        okSelectId: "",
      },
      teacherQuota: 0,
      followStudentSize:
        getCurrentInstance()?.appContext.config.globalProperties.$teacherStNum,
      tempStudentFrom: {},
      studentFromVisible: false,
      isEdit: true,
      studenFileVisible: false,
      exportForm: {teacherName: '', stuClass: ''},

      DownStudentFile: (flag) => {
        let fileUrl = "";
        if (flag == 1) {
          fileUrl =
            state.downloadUrl +
            "/" +
            state.tempStudentFrom.file.replace(/\\/g, "/");
        } else if (flag == 2) {
          fileUrl =
            state.downloadUrl +
            "/" +
            state.tempStudentFrom.file2.replace(/\\/g, "/");
        } else if (flag == 3) {
          fileUrl =
            state.downloadUrl +
            "/" +
            state.tempStudentFrom.file3.replace(/\\/g, "/");
        }
        window.open(fileUrl, "_blank");
      },

      ExportFormButton: ()=>{
        state.exportForm.teacherName = localStorage.getItem("name");
        let flag = confirm(" [" + state.exportForm.teacherName  + "] !")
        if(flag){
          axios({
            url: '/excel/getAll?teacherName=' + state.exportForm.teacherName + '&stuClass='
          }).then(function(resp){
          
            alert(", !")
            let fileUrl = state.downloadUrl + resp.data
            window.open(fileUrl, "_blank");
          }).catch((error)=>{
                 ElMessage({
              message: ", !" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          })
        }
      },

      queryStudentList() {
        let teacherId = localStorage.getItem("id");
        axios({
          method: "get",
          url: "/teacher/student?teacherId=" + teacherId + "&isSelected=1",
        })
          .then(function (resp) {
            // console.log(resp)
            // state.page.studentCount = resp.data['count']
            // state.studentList = resp.data['student']
            state.studentList = resp.data;
            ElMessage({
              message: "",
              showClose: true,
              grouping: true,
              duration: 1000,
              type: "success",
            });
          })
          .catch((error) => {
            ElMessage({
              message: "" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
      emptyClick() {
        state.searchData.studentId = "";
        state.searchData.name = "";
        state.searchData.title = "";
      },
      resetList() {
        state.searchData.studentId = "";
        state.searchData.name = "";
        state.searchData.title = "";
        state.queryStudentList();
      },

      handleSelectionChangeButton(val) {
        state.multipleSelectionData = val;
        // ElMessageBox.confirm(val)
      },
      multipleSelectButton() {
        // confirm(state.multipleSelectionData[0]['name'])

        //
        let starr = state.multipleSelectionData;

        if (starr.length > 20) {
          alert("20");
          return;
        }

        let okSelect = 0;
        let okSelectName = "[";
        let okSelectId = "";
        for (let i = 0; i < starr.length; i++) {
          if (starr[i]["mentorId"] == 0) {
            okSelect = okSelect + 1;
            okSelectName = okSelectName + starr[i]["name"] + ",";
            okSelectId = okSelectId + starr[i]["studentId"] + ",";
          }
        }

        okSelectName = okSelectName + "]";
        if (okSelect != 0) {
          let teacherId = localStorage.getItem("id");
          let flag = confirm(
            " " +
              okSelectName +
              okSelect +
              "  !"
          );
          if (flag) {
            axios({
              url:
                "/teacher/select?teacherId=" +
                teacherId +
                "&studentId=" +
                okSelectId,
            })
              .then(function (resp) {
                if (resp.data == true) {
                  alert("");
                } else {
                  alert(
                    ""
                  );
                }
              })
              .catch(() => {
                alert("");
              });
          }
        } else if (starr.length != 0) {
          confirm("");
        }
      },
      queryTeacherData() {
        let teacherId = localStorage.getItem("id");
        axios({
          url: "/teacher/get/one?teacherId=" + teacherId,
        })
          .then(function (resp) {
            state.teacherQuota = resp.data["studentNum"];
          })
          .catch((error) => {
            ElMessage({
              message: "!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },

      handleSizeChange(val) {
        state.page.pageSize = val;
        state.queryStudentList();
      },
      handleCurrentChange(val) {
        state.page.currentPage = val;
        state.queryStudentList();
      },

      teacherModStudent(st, flag) {
        state.tempStudentFrom = JSON.parse(JSON.stringify(st));
        if (flag == 1) {
          state.studentFromVisible = true;
        } else if (flag == 2) {
          state.studenFileVisible = true;
        }
      },
      queryModStudentData() {
        state.studentFromVisible = false;
        state.studenFileVisible = false;
        axios({
          method: "put",
          url: "/student",
          data: state.tempStudentFrom,
        })
          .then(function (resp) {
            state.queryStudentList();
            if (resp.data == true) {
              ElMessage({
                message: "!",
                showClose: true,
                grouping: true,
                duration: 1000,

                type: "success",
              });
            } else {
              ElMessage({
                message: "!",
                showClose: true,
                grouping: true,
                duration: 2000,

                type: "warning",
              });
            }
          })
          .catch((error) => {
            ElMessage({
              message: "! " + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
    });

    onMounted(() => {
      state.queryStudentList();
      state.queryTeacherData();
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>


<style scoped>
.el-alert {
  margin: 20px 0 0;
}
.el-alert:first-child {
  margin: 0;
}
</style>



