<script>
import axios from "axios";
import { reactive, toRefs, onMounted, getCurrentInstance } from "vue";
import { ElMessage } from "element-plus";

export default {
  name: "StudentList",
  setup() {
    const state = reactive({
      slogan: "",
      downloadUrl:
        getCurrentInstance()?.appContext.config.globalProperties.$downloadUrl,
      searchData: {
        studentId: "",
        name: "",
        title: "",
        tutorFalg: "",
        stuClass: "",
        teacherName: "",
      },
      studentList: [],
      // studentTotal: 20,
      multipleSelectionData: [],
      page: { currentPage: 1, pageSize: 25, studentCount: 0 },
      formatSelect: {
        allSelect: 0,
        notSelect: 0,
        okSelect: 0,
        notSelectName: "",
        okSelectName: "",
        okSelectId: "",
      },
      teacherQuota: 0,
      followStudentSize:
        getCurrentInstance()?.appContext.config.globalProperties.$teacherStNum,
      userType: 0, //userType
      tempStudentData: {},
      adminModStudentVisible: false,
      paperFormVisible: false,
      exportView: false,
      exportForm: {
        teacherName: "",
        stuClass: "",
        tips: "!",
      },

      ExportFormButton: () => {
        let flag = confirm("!");
        if (flag) {
          axios({
            url:
              "/excel/getAll?teacherName=" +
              state.exportForm.teacherName +
              "&stuClass=" + state.exportForm.stuClass,
          })
            .then(function (resp) {
          
              alert(", !")
              let fileUrl = state.downloadUrl + resp.data;
              window.open(fileUrl, "_blank");
            })
            .catch((error) => {
              ElMessage({
                message: ", !" + error,
                showClose: true,
                grouping: true,
                duration: 2000,
                type: "error",
              });
            });
        }
      },

      queryStudentList() {
        axios({
          method: "get",
          url:
            "/student/search?name=" +
            state.searchData.name +
            "&studentId=" +
            state.searchData.studentId +
            "&stuClass=" +
            state.searchData.stuClass +
            "&teacherName=" +
            state.searchData.teacherName +
            "&title=" +
            state.searchData.title +
            "&startIndex=" +
            state.page.currentPage +
            "&pageSize=" +
            state.page.pageSize,
        })
          .then(function (resp) {
            // console.log(resp)
            state.page.studentCount = resp.data["count"];
            state.studentList = resp.data["student"];
            ElMessage({
              message: "",
              showClose: true,
              grouping: true,
              duration: 1000,
              type: "success",
            });
          })
          .catch((error) => {
            // console.log('!')
            ElMessage({
              message: "!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
      revokeTutor: () => {
        let flag = confirm(
          " [" + state.tempStudentData.name + "] ?"
        );
        if (flag) {
          axios({
            url: "/teacher/refuse?studentId=" + state.tempStudentData.studentId,
          })
            .then(function (resp) {
              state.adminModStudentVisible = false;
              state.queryStudentList();
              state.queryTeacherData();
              if (resp.data == true) {
                ElMessage({
                  message: "!",
                  showClose: true,
                  grouping: true,
                  duration: 1000,
                  type: "success",
                });
              }
            })
            .catch((error) => {
              state.queryStudentList();
              ElMessage({
                message: ",!" + error,
                showClose: true,
                grouping: true,
                duration: 2000,
                type: "error",
              });
            });
        }
      },
      emptyClick() {
        state.searchData.studentId = "";
        state.searchData.name = "";
        state.searchData.title = "";
      },
      resetList() {
        state.searchData.studentId = "";
        state.searchData.name = "";
        state.searchData.title = "";
        state.queryStudentList();
      },

      handleSelectionChangeButton(val) {
        state.multipleSelectionData = val;
        // ElMessageBox.confirm(val)
      },
      multipleSelectButton() {
        //
        let starr = state.multipleSelectionData;
        state.queryTeacherData();
        if (starr.length > state.followStudentSize) {
          alert("" + state.followStudentSize + "!");
          return;
        } else if (
          starr.length >
          state.followStudentSize - state.teacherQuota
        ) {
          alert("");
          return;
        } else if (starr.length <= 0) {
          return;
        }

        let okSelect = 0;
        let okSelectName = "[";
        let okSelectId = "";
        for (let i = 0; i < starr.length; i++) {
          if (starr[i]["mentorId"] == 0) {
            okSelect = okSelect + 1;
            okSelectName = okSelectName + starr[i]["name"] + ",";
            okSelectId = okSelectId + starr[i]["studentId"] + ",";
          }
        }
        okSelectName = okSelectName + "]";
        if (okSelect != 0) {
          let teacherId = localStorage.getItem("id");
          let flag = confirm(
            " " +
              okSelectName +
              okSelect +
              "  !"
          );
          if (flag) {
            axios({
              url:
                "/teacher/select?teacherId=" +
                teacherId +
                "&studentId=" +
                okSelectId,
            })
              .then(function (resp) {
                state.queryTeacherData();
                state.queryStudentList();
                if (resp.data == true) {
                  alert("");
                } else {
                  alert(
                    ""
                  );
                }
              })
              .catch((error) => {
                alert("!" + error);
              });
          }
        } else if (starr.length != 0) {
          confirm("");
        }
      },
      queryTeacherData() {
        let teacherId = localStorage.getItem("id");
        axios({
          url: "/teacher/get/one?teacherId=" + teacherId,
        })
          .then(function (resp) {
            state.teacherQuota = resp.data["studentNum"];
          })
          .catch((error) => {
            alert("!" + error);
          });
      },

      handleSizeChange(val) {
        state.page.pageSize = val;
        state.queryStudentList();
      },
      handleCurrentChange(val) {
        state.page.currentPage = val;
        state.queryStudentList();
      },
      adminModStudentFrom(st, flag) {
        if (flag == 1) {
          state.adminModStudentVisible = true;
        } else if (flag == 2) {
          state.paperFormVisible = true;
        }
        state.tempStudentData = JSON.parse(JSON.stringify(st));
      },
      queryModData() {
        state.adminModStudentVisible = false;
        state.paperFormVisible = false;
        axios({
          method: "put",
          url: "/student",
          data: state.tempStudentData,
        })
          .then(function (resp) {
            state.queryStudentList();
            if (resp.data == true) {
              ElMessage({
                message: "!",
                showClose: true,
                grouping: true,
                duration: 1000,
                type: "success",
              });
            } else {
              ElMessage({
                message: "!",
                showClose: true,
                grouping: true,
                duration: 2000,
                type: "warning",
              });
            }
          })
          .catch((error) => {
            ElMessage({
              message: "! " + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
    });

    onMounted(() => {
      state.queryStudentList();
      state.queryTeacherData();
      state.userType = localStorage.getItem("type");
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>



<style scoped>
.dialog-footer button:first-child {
  margin-right: 10px;
}
</style>