<template>
  <div id="loginPage">
    <h1></h1>

    <div id="loginDiv">
      <el-form :inline="true" :model="userData" class="demo-form-inline">
        <el-form-item label="">
          <el-input v-model="userData.id" placeholder="" />
        </el-form-item>
        <br />
        <el-form-item label="">
          <el-input
            type="password"
            v-model="userData.passWord"
            placeholder=""
          />
        </el-form-item>
        <br />
        <el-form-item label="">
          <el-select v-model="userData.flag" placeholder="">
            <el-option label="" value="1" />
            <el-option label="" value="2" />
          </el-select>
        </el-form-item>
        <br />
        <el-form-item>
          <el-button type="primary" @click="LoginButton()"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <div>
      <el-dialog v-model="dialogVisible" title="Tips" width="30%">
        <span></span>
        <template #footer>
          <span class="dialog-footer">
            <el-button @click="dialogVisible = false">Cancel</el-button>
            <el-button type="primary" @click="dialogVisible = false"
              >Confirm</el-button
            >
          </span>
        </template>
      </el-dialog>
    </div>

    <div>
      <el-tag class="ml-2" type="success"
        >Edge</el-tag
      ><el-divider direction="vertical" border-style="dashed" />

      <el-tag class="ml-2" type="success"
        ></el-tag
      >
      <el-divider direction="vertical" border-style="dashed" />

      <el-tag class="ml-2" type="success"></el-tag>
    </div>
  </div>
</template>
  
<script >
import router from "@/router";
import axios from "axios";
import { reactive, toRefs, onMounted } from "vue";
import { useRouter } from "vue-router";
import { ElMessage } from "element-plus";

export default {
  setup() {
    const state = reactive({
      userData: { id: "", passWord: "", flag: "" }, // =1 =2 =3
      dialogVisible: false,

      router: useRouter(),

      LoginButton() {
        if (
          state.userData.id.trim() == "" ||
          state.userData.passWord.trim() == ""
        ) {
          state.dialogVisible = true;
          return;
        }
        // 
        if (state.userData.flag == 1) {
          state.loginQuery("/teacher/login", 1);
        } // 
        else if (state.userData.flag == 2) {
          state.loginQuery("/student/login", 2);
        }
      },

      loginQuery(url, type) {
        axios
          .post(url, {
            id: state.userData.id,
            passWord: state.userData.passWord,
          })
          .then(function (resp) {
            let result = resp.data;

            if (result["token"] == null) {
              alert("");
              return;
            }

            localStorage.setItem("token", result["token"]);

            if (type == 1) {
              localStorage.setItem("name", result["teacher"]["name"]);
              localStorage.setItem("id", result["teacher"]["id"]);
              localStorage.setItem("type", 1);
              if (result["teacher"]["id"] == "100607") {
                localStorage.setItem("type", 3);
              }
              router.push("teacherView");
            } else {
              localStorage.setItem("name", result["student"]["name"]);
              localStorage.setItem("id", result["student"]["studentId"]);
              localStorage.setItem("type", 2);
              router.push("studentView");
            }
          })
          .catch((error) => {
            alert("" + error);
          });
      },
    });

    onMounted(() => {
      // 1  2 3
      // localStorage.setItem("token", 1312323)
      // localStorage.setItem("id", 1)
      // localStorage.setItem("id", 201905010147)
      // localStorage.setItem("type", 2)

      if (!window.localStorage) {
        alert("localstorage Edge  ");
        //  Edge
        return; /////////////
      }
      let token = localStorage.getItem("token");
      let id = localStorage.getItem("id");
      let userName = localStorage.getItem("name");
      if (token != null && id != null) {
        ElMessage({
          message: userName + "!",
          showClose: true,
          grouping: true,
          duration: 1000,
          type: "success",
        });

        let userType = localStorage.getItem("type");
        if (userType == 1 || userType == 3) {
          router.push("teacherView");
        } else if (userType == 2) {
          router.push("studentView");
        }
      }
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>


<style scoped>
/* #loginPage{
  background: url(../assets/imgs/Desert.jpg);
} */

#loginDiv {
  width: 50%;
  margin: 5% auto;
}

.dialog-footer button:first-child {
  margin-right: 10px;
}
</style>
