<template>
  <div id="pageView">
    <el-form :inline="true" :model="searchData" class="demo-form-inline">
      <!-- <el-form-item label="">
                <el-input v-model="searchData.jobNo" placeholder="" />
            </el-form-item> -->

      <el-form-item label="">
        <el-input v-model="searchData.name" placeholder="" />
      </el-form-item>

      <!-- <el-form-item label="">
                <el-input v-model="searchData.title" placeholder="" />
            </el-form-item> -->

      <el-form-item>
        <el-button type="primary" @click="queryTeacherList()"></el-button>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="emptyClick()"></el-button>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="resetList()"></el-button>
      </el-form-item>

      <el-form-item v-if="userType == 2">
        <el-button type="danger"></el-button>
      </el-form-item>

      <el-form-item v-if="userType == 2">
        <el-button type="danger">
          
        </el-button>
      </el-form-item>

      <!--  -->
      <el-form-item>
        <el-button
          type="primary"
          v-if="userType == 3"
          @click="multipleSelectButton()"
          ></el-button
        >
      </el-form-item>
    </el-form>

    <!--  -->
    <el-dialog
      v-model="dialogVisible"
      title="Tips"
      width="30%"
      :before-close="handleClose"
    >
      <span>This is a message</span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="dialogVisible = false"
            >Confirm</el-button
          >
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-table
      :data="teacherList"
      stripe
      style="width: 100%"
      @selection-change="handleSelectionChangeButton"
    >
      <el-table-column type="selection" width="55"></el-table-column>

      <el-table-column type="index" align="center" width="60" label="" />

      <el-table-column align="center" prop="name" label="" />

      <el-table-column align="center" prop="id" label="" />

      <el-table-column align="center" prop="studentNum" label="" />

      <el-table-column align="center" prop="major" label="" />

      <el-table-column align="center" prop="academicDegree" label="" />

      <el-table-column align="center" prop="teacherTitle" label="" />

      <el-table-column align="center" prop="phone" label="" />

      <el-table-column align="center" prop="email" label="" />

      <el-table-column align="center" label="">
        <template #default="scope">
          <el-button type="success">{{
            studentTotal - scope.row.studentNum
          }}</el-button>

          <!--   -->
          <el-button
            type="primary"
            v-if="
              studentTotal - scope.row.studentNum > 0 &&
              currentStudent.mentorId == 0 &&
              userType == 2
            "
            @click="studentSelectStudent(scope.row)"
            ></el-button
          >

          <el-button
            type="warning"
            v-else-if="currentStudent.mentorId != 0 && userType == 2"
          >
            
          </el-button>

          <el-button type="warning" v-else-if="userType == 2"></el-button>
        </template>
      </el-table-column>

      <!-- <el-table-column prop="address" label="Address" /> -->
    </el-table>

    <el-pagination
      v-model:currentPage="page.currentPage"
      v-model:page-size="page.pageSize"
      :page-sizes="[20, 50, 80, 120]"
      :small="small"
      :disabled="disabled"
      :background="background"
      layout="total, sizes, prev, pager, next, jumper"
      :total="page.teacherCount"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    />
  </div>
</template>


<script>
import axios from "axios";
import { reactive, toRefs, onMounted, getCurrentInstance } from "vue";
import { ElMessageBox, ElMessage } from "element-plus";

export default {
  name: "TeacherList",
  setup() {
    const state = reactive({
      msg: "",
      searchData: { id: "", name: "", title: "" },
      dialogVisible: false,
      teacherList: [],
      currentStudent: {},
      studentTotal:
        getCurrentInstance()?.appContext.config.globalProperties.$teacherStNum,
      multipleSelectionData: [],
      page: { currentPage: 1, pageSize: 25, teacherCount: 0 },
      userType: 0, //2 3 1

      queryTeacherList() {
        //?name &  startIndex & pageSize
        axios({
          method: "get",
          url:
            "/teacher?name=" +
            state.searchData.name +
            "&startIndex=" +
            state.page.currentPage +
            "&pageSize=" +
            state.page.pageSize,
        })
          .then(function (resp) {
            // console.log(resp)

            ElMessage({
              message: "!",
              showClose: true,
              grouping: true,
              duration: 1000,
              type: "success",
            });
            state.page.teacherCount = resp.data["count"];
            state.teacherList = resp.data["teacher"];
            // state.page.teacherCount = resp.data['count']
          })
          .catch((error) => {
            ElMessage({
              message: "!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
      emptyClick() {
        state.searchData.id = "";
        state.searchData.name = "";
        state.searchData.title = "";
      },
      resetList() {
        state.searchData.id = "";
        state.searchData.name = "";
        state.searchData.title = "";
        state.queryTeacherList();
      },

      handleSelectionChangeButton(val) {
        state.multipleSelectionData = val;
        // ElMessageBox.confirm(val)
      },
      multipleSelectButton() {
        ElMessageBox.confirm(state.multipleSelectionData);
        //
      },

      handleSizeChange(val) {
        state.page.pageSize = val;
        state.queryTeacherList();
      },
      handleCurrentChange(val) {
        state.page.currentPage = val;
        state.queryTeacherList();
      },
      studentSelectStudent(teacher) {
        let stId = localStorage.getItem("id");
        let tId = teacher.id;

        axios({
          url: "/student/select?teacherId=" + tId + "&studentId=" + stId,
        })
          .then(function (resp) {
            state.queryTeacherList();
            if (resp.data == true) {
              ElMessage({
                message: "!",
                showClose: true,
                grouping: true,
                duration: 1000,
                type: "success",
              });
            } else {
              ElMessage({
                message: "!",
                showClose: true,
                grouping: true,
                duration: 2000,
                type: "warning",
              });
            }
          })
          .catch((error) => {
            ElMessage({
              message: "!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
    });

    onMounted(() => {
      state.queryTeacherList();
      state.userType = localStorage.getItem("type");

      if (state.userType == 2) {
        let stId = localStorage.getItem("id");
        axios({
          url:
            "/student/search?name=" +
              "&stuClass=" +
            "&teacherName=" +
            "&studentId=" +
            stId +
            "&title=" +
            
            "&startIndex=1&pageSize=25",
        })
          .then(function (resp) {
            state.currentStudent = resp.data["student"][0];
          })
          .catch((error) => {
            ElMessage({
              message: "!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      }
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>



<style>
.dialog-footer button:first-child {
  margin-right: 10px;
}
</style>