<template>
  <div id="pageView">
    <el-form :inline="true" :model="searchData" class="demo-form-inline">
      <!-- <el-form-item label="">
        <el-input v-model="searchData.studentId" placeholder="" />
      </el-form-item> -->

      <!-- <el-form-item label="">
        <el-input v-model="searchData.name" placeholder="" />
      </el-form-item> -->

      <!-- <el-form-item label="">
        <el-input v-model="searchData.title" placeholder="" />
      </el-form-item> -->

      <!-- <el-form-item>
        <el-button type="primary" @click="queryStudentList()"></el-button>
      </el-form-item> -->

      <!-- <el-form-item>
        <el-button type="primary" @click="emptyClick()"></el-button>
      </el-form-item> -->

      <el-form-item>
        <el-button type="warning"
          >{{ followStudentSize }}{{ teacherQuota }}{{
            followStudentSize - teacherQuota
          }}</el-button
        >
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="resetList()"></el-button>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="multipleSelectButton()"
          ></el-button
        >
      </el-form-item>
    </el-form>

    <!--  -->
    <el-table
      :data="studentList"
      stripe
      style="width: 100%"
      @selection-change="handleSelectionChangeButton"
    >
      <el-table-column type="selection" width="55"></el-table-column>

      <el-table-column type="index" align="center" width="60" label="" />

      <el-table-column align="center" prop="name" label="" />

      <el-table-column align="center" prop="studentId" label="" />

      <el-table-column align="center" prop="specialities" label="" />

      <el-table-column align="center" prop="stuClass" label="" />

      <el-table-column align="center" prop="phone" label="" />

      <el-table-column align="center" prop="email" label="" />

      <el-table-column align="center" label="">
        <template #default="scope">
          <el-button type="primary" v-if="scope.row.isSelected == 0"
            ></el-button
          >
          <el-button type="success" v-else-if="scope.row.isSelected"
            ></el-button
          >
          <el-button type="warning" @click="refuse(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- <div>
        <el-pagination v-model:currentPage="page.currentPage" v-model:page-size="page.pageSize"
      :page-sizes="[20, 50, 80, 120]" :small="small" :disabled="disabled" :background="background"
      layout="total, sizes, prev, pager, next, jumper" :total="page.studentCount" @size-change="handleSizeChange"
      @current-change="handleCurrentChange" />
    </div> -->
  </div>
</template>


<script>
import axios from "axios";
import { reactive, toRefs, onMounted, getCurrentInstance } from "vue";
import { ElMessage } from "element-plus";

export default {
  name: "StudentApplicationList",
  setup() {
    const state = reactive({
      slogan: "",
      searchData: { studentId: "", name: "", title: "", tutorFalg: "" },
      dialogVisible: false,
      studentList: [],
      // studentTotal: 20,
      multipleSelectionData: [],
      page: { currentPage: 1, pageSize: 25, studentCount: 0 },
      formatSelect: {
        allSelect: 0,
        notSelect: 0,
        okSelect: 0,
        notSelectName: "",
        okSelectName: "",
        okSelectId: "",
      },
      teacherQuota: 0,
      followStudentSize:
        getCurrentInstance()?.appContext.config.globalProperties.$teacherStNum,

      queryStudentList() {
        let teacherId = localStorage.getItem("id");
        axios({
          method: "get",
          url: "/teacher/student?teacherId=" + teacherId + "&isSelected=0",
        })
          .then(function (resp) {
            // console.log(resp)
            // state.page.studentCount = resp.data['count']
            // state.studentList = resp.data['student']
            state.studentList = resp.data;
            ElMessage({
              message: "!",
              showClose: true,
              grouping: true,
              duration: 1000,
              type: "success",
            });
          })
          .catch((error) => {
            // console.log('!')
            ElMessage({
              message: "!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },
      queryTeacherData() {
        let teacherId = localStorage.getItem("id");
        axios({
          url: "/teacher/get/one?teacherId=" + teacherId,
        })
          .then(function (resp) {
            state.teacherQuota = resp.data["studentNum"];
          })
          .catch((error) => {
            ElMessage({
              message: "!" + error,
              showClose: true,
              grouping: true,
              duration: 2000,
              type: "error",
            });
          });
      },

      emptyClick() {
        state.searchData.studentId = "";
        state.searchData.name = "";
        state.searchData.title = "";
      },
      resetList() {
        state.searchData.studentId = "";
        state.searchData.name = "";
        state.searchData.title = "";
        state.queryStudentList();
      },

      handleSelectionChangeButton(val) {
        state.multipleSelectionData = val;
        // ElMessageBox.confirm(val)
      },
      multipleSelectButton() {
        //
        let starr = state.multipleSelectionData;
        state.queryTeacherData();
        if (starr.length > state.followStudentSize) {
          alert("" + state.followStudentSize + "!");
          return;
        } else if (
          starr.length >
          state.followStudentSize - state.teacherQuota
        ) {
          alert("");
          return;
        } else if (starr.length <= 0) {
          return;
        }

        let okSelectName = "[";
        let okSelectId = "";
        for (let i = 0; i < starr.length; i++) {
          okSelectName = okSelectName + starr[i]["name"] + ",";
          okSelectId = okSelectId + starr[i]["studentId"] + ",";
        }
        okSelectName = okSelectName + "]";

        let teacherId = localStorage.getItem("id");
        let flag = confirm(
          " " +
            okSelectName +
            "  " +
            starr.length +
            " "
        );
        if (flag) {
          axios({
            url:
              "/teacher/select?teacherId=" +
              teacherId +
              "&studentId=" +
              okSelectId,
          })
            .then(function (resp) {
              state.queryTeacherData();
              state.queryStudentList();
              if (resp.data == true) {
                alert("!");
              } else {
                alert(
                  ""
                );
              }
            })
            .catch(() => {
              alert("");
            });

          // setTimeout(state.queryStudentList(), 3000);
        }
      },
      refuse(st) {
        let stName = st.name;
        let stId = st.studentId;
        if (confirm(" [" + stName + " ] ?")) {
          axios({
            url: "/teacher/refuse?studentId=" + stId,
          })
            .then(function (resp) {
              state.queryTeacherData();
              state.queryStudentList();
              if (resp.data == true) {
                ElMessage({
                  message: "!",
                  showClose: true,
                  grouping: true,
                  duration: 1000,
                  type: "success",
                });
              }
            })
            .catch((error) => {
              state.queryTeacherData();
              state.queryStudentList();
                 ElMessage({
                  message: ",!" + error,
                  showClose: true,
                  grouping: true,
                  duration: 2000,
                  type: "error",
                });
            });
        }
      },

      handleSizeChange(val) {
        state.page.pageSize = val;
        state.queryStudentList();
      },
      handleCurrentChange(val) {
        state.page.currentPage = val;
        state.queryStudentList();
      },
    });

    onMounted(() => {
      state.queryStudentList();
      state.queryTeacherData();
    });

    return {
      ...toRefs(state),
    };
  },
};
</script>



